import React, {useState, componentDidMount, useEffect} from "react";
import "./App.css";

import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";

import StoreFinder from './components/storeFinder/storeFinder'
import Menu from './components/menu/menu'
import NavBar from './components/navbar/NavBar'

// let DEV_URL = '';

// if (process.env.NODE_ENV === 'development') {
//   DEV_URL = 'http://localhost:3000';
// }

function App() {
  
  // Creates an overarching empty state that looks for
  const [storeSelected, setStoreSelected] = useState({
    storeId: '',
  });

  // useEffect(() => {
  //   (async () => {
  //     const res = await fetch (`${DEV_URL}/locations`)
  //   })()
  
  // }, [])


  // If there is no store ID return the storeFinder component 
  return (
    
    <Router basename="/locations">
      <div className='holder'>

        <NavBar></NavBar>
        <Switch>
          <Route exact path="/menu/:id" >
            <Menu Id={storeSelected.storeId} callBack={() => setStoreSelected}></Menu>
          </Route>
          <Route exact path='/:store'>
            <StoreFinder callBack={setStoreSelected}></StoreFinder>
          </Route>
          <Route exact path='/'>
            <StoreFinder callBack={setStoreSelected}></StoreFinder>
          </Route>
        </Switch>
      </div>
    </Router>
  );

}


export default App;

