import React from 'react';
import './navbar.css';
// import Logo from './../images/cropped-TOPNAVLOGO.png';

const Navbar=() => {
  return (
    <>
   

        <div id="navbar">
            <div className="row">
                <div className="col-6">
                    <a href="/" className="navLogo">
                        <img src="/images/homeImages/cropped-TOPNAVLOGO.png" />
                    </a>
                </div>
                <div class="col-6">
                <a href="/" class="mainNavItem">Home</a>
                <a href="/colorado-deals" class="mainNavItem">Deals</a>
                <a href="/locations" class="mainNavItem">Shop</a>
                </div>
            </div>
        </div>
        
    </>
  )
};
export default Navbar;